import React, {Component} from 'react';
import './App.css';

import {backgroundClasses} from './backgrounds';

import sample from 'lodash/sample';


import {Button, Grid, Panel, Row} from 'react-bootstrap';


const QUOTES_URL = 'https://gist.githubusercontent.com/j-chimienti/2985bbb764c968a2c6232711b23a0b1e/raw/4334ba81e06aca35605a1eb10c44cb7c5d236d10/quotes.json';


class App extends React.Component {


    constructor(props) {

        super();

        this.state = {
            quotes: [],
            currentQuote: null,
            history: [],
            backgroundGradient: sample(backgroundClasses),
        };

        this.newQuote = this.newQuote.bind(this);
        this.fetchQuotes = this.fetchQuotes.bind(this);

    }

    componentDidMount() {

        document.addEventListener('keydown', (e) => {


            // keyCode or which

            if (e.keyCode === 39) {

                this.newQuote();
            }
        });


        this.fetchQuotes();
    }

    fetchQuotes() {

        const options = {
            method: 'get',

            headers: {
                accept: 'application/json'
            }
        };

        fetch(QUOTES_URL, options)
            .then(response => response.json())
            .then(result => {

                const quotes = result.map(r => {
                    const [quote, author] = r;

                    return {quote, author};
                });

                this.setState({
                    quotes,
                    currentQuote: sample(quotes)
                })

            })
            .then(console.log)
            .catch(console.error)
    }


    newQuote() {


        const {
            history,
            currentQuote,
            quotes,
            backgroundGradient
        } = this.state;

        const updatedHistory = [...history, currentQuote];

        let newQuote_ = sample(quotes);


        while (updatedHistory
            .slice(-5)
            .find(h => h === newQuote_)) {

            newQuote_ = sample(quotes);
        }


        const newGradient = changeBackground();

        this.setState({
            ...this.state,
            currentQuote: newQuote_,
            history: updatedHistory,
            backgroundGradient: newGradient,
        });

        function changeBackground() {

            let newGradient = newBackground();

            while (backgroundGradient === newGradient) {

                newGradient = newBackground();
            }

            return newGradient;

            function newBackground() {

                return sample(backgroundClasses);
            }

        }


    }

    mapQuoteToTweet() {

        const {backgroundGradient, currentQuote: {quote, author}} = this.state;

        return encodeURIComponent(`${quote} \n ${author}`);
    }


    render() {


        const {quotes} = this.state;

        if (!(Array.isArray(quotes) && quotes.length > 0)) {

            return <div>Loading...</div>
        }

        const {backgroundGradient, currentQuote: {quote, author}} = this.state;
        const author_ = <span>
            <i className={'fa fa-pencil'}></i>
            {' '}
            {author}
        </span>;

        const TWEET_TEXT = this.mapQuoteToTweet();

        const DOWNLOAD_URI = `data:text/html,${encodeURIComponent(JSON.stringify(quotes, null, 4))}`

        return (
            <div className={backgroundGradient + ' app text-center container-fluid'}

            >

                <Grid>
                    <Row className={'quote_display'}>
                        <Panel header={author_}>

                            <i className={'fa fa-quote-left'}></i>
                            {' '}
                            {quote}
                        </Panel>
                    </Row>

                    <Row

                        className={'social_row'}>

                        <a className="btn btn-outline-primary"
                           href={`https://twitter.com/intent/tweet?text=${TWEET_TEXT}`}

                        >
                            <i className={'fa fa-twitter fa-2x text-white'}></i>
                        </a>
                        <a href={'https://gist.github.com/j-chimienti/2985bbb764c968a2c6232711b23a0b1e'}
                           target={'blank_'}
                        >
                            <i className={'fa fa-github fa-2x text-white'}>
                            </i>
                        </a>

                        <a download={'quotes.json'} href={DOWNLOAD_URI}>
                            <i className={'fa fa-download fa-2x text-white'}></i>
                        </a>

                    </Row>
                </Grid>


                <Button
                    bsStyle={'primary'}
                    bsSize={'large'}
                    onClick={this.newQuote}
                    className={"new_quote_btn"}
                >

                    <i className={'fa fa-arrow-right fa-2x'}></i>
                </Button>


            </div>
        );
    }
}


export default App;



